.modal-film {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 280px;
  max-height: 968px;
  height: 100vh;
  // height: 100%;
  margin: auto;
  margin-bottom: 80px;
  overflow: auto;
  border: 1px solid rgba(0, 0, 0, 0);
  background-color: #fff;
}
.modal-film__img-cont {
  margin: auto;
  margin-top: 48px;
  width: 240px;
  height: 357px;
  // overflow: hidden;
  border-radius: 4px;
}

.modal-film__img {
  display: block;
  height: 357px;
  margin: auto;
  object-fit: contain;
}

.modal-film__text-cont {
  margin-top: 40px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 40px;

  &__name {
    font-size: 20px;
    line-height: 1.17;
    font-weight: 500;
    padding-bottom: 20px;
    // margin-top: 30px;
    text-transform: uppercase;
  }
}
