.modal-film__text-cont__about {
  text-transform: uppercase;
  font-weight: 700;
  color: #000000;
  padding-top: 10px;
}
.modal-film__text-cont__about-description {
  padding-top: 10px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  color: #000000;
}
.modal-film__text-cont__cont {
  padding-bottom: 21px;
}

.button-modal-wrapper {
  padding: 10px;
  display: flex;
  align-items: center;
}

.button-modal {
  // all: unset;
  width: 110px;
  min-height: 44px;
  background-color: white;
  border: 1px solid black;
  text-transform: uppercase;
  font-family: Roboto;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  border-radius: 6px;
  text-align: center;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 10px;
  }

  &:hover {
    background-color: #ff6b08;
    border: 1px solid #ff6b08;
    color: #fff;
  }
}

.modal-close-btn {
  position: absolute;

  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;

  background: $backgroundColorLight;
  border-radius: 50%;
  border: 0;

  &:hover .close {
    opacity: 1;
  }
}

.close {
  position: absolute;
  right: 15px;
  top: 10px;
  width: 14px;
  height: 14px;
  opacity: 0.3;

  &:before,
  &:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 15px;
    width: 2px;
    background-color: $backgroundColorDark;
  }

  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }
}

