@media screen and (min-width: 768px) {
  .modal-film {
    margin-bottom: 80px;
    display: flex;
    // overflow: auto;
    width: 620px;
    height: 562px;
  }
  .modal-film__img-cont {
    margin-right: 30px;
    margin-left: 30px;
    width: 264px;
    height: 372px;
  }
  .modal-film__img {
    width: 264px;
    height: 372px;
  }

  .modal-film__text-cont {
    width: 295px;
  }

  .modal-film__text-cont__name {
    margin: 4px 0 22px;
    font-size: 30px;
  }

  .button-modal {
    width: 125px;
    height: 44px;
  }
  .button-watched {
    margin-right: 15px;
  }
}

@media screen and (min-width: 1024px) {
  .modal-film {
    min-width: 882px;
  }

  .modal-film__img-cont {
    width: 396px;
    height: 477px;
  }

  .modal-film__img {
    width: 396px;
    height: 477px;
  }
  .modal-film__text-cont {
    width: 423px;
    margin-left: 0px;
    margin-right: 40px;
  }
}
